*{
    font-family: 'Public Sans', sans-serif;

}
.home-main{
    display: flex;
    flex-direction: row;
    background-color: white;
    min-height: 101vh;
    min-width: 100%;
    align-items: flex-start;
    justify-content: flex-start;
    color: white;
}
.home-main-left{
    display: flex;
    min-width: 60px;
}
.home-main-right{
    min-width: calc(100%-30px);
    min-height: 100vh;
}
.home-main-container{
    min-height: 100vh;
    min-width: 100%;
}

.read-more-button{
    font-size: 16px;
    border-radius: 5px !important;
}
.read-more-button:hover{
    color: #c5262d !important;
}