    list-style: none;
    margin: 0;
    padding: 0;
    text-align: center;
    position: relative;
    z-index: 2;
  }
  
  .product-section .ps-list li {
    /* width: 25%
    float: left;; */
    height: 285px;
    border-right: 1px solid rgba(255,255,255,.5);
    border-bottom: 1px solid rgba(255,255,255,.5);
    color: #fff;
    padding:0;
  }
  
  .product-section .ps-list li .ps-list-content {
    height: 100%;
    width: 100%;
